body, html {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    font-family: Arial, sans-serif;
}

.centered-div {
    background-color: #ffffff;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    text-align: center;
    min-width: 500px;
    max-width: 700px;
    margin: 10px;
    word-break: break-word;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 10px 0;
    color: #333;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.25);
}

h1 {
    font-size: 2.5em;
    font-weight: 700;
    color: #2c3e50;
    border-bottom: 2px solid #2c3e50;
    padding-bottom: 10px;
    margin-bottom: 20px;
    /*box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);*/
    border-radius: 5px;
    /*background: linear-gradient(45deg, #ecf0f1, #bdc3c7);*/
}

h2 {
    font-size: 2em;
    font-weight: 600;
    color: #34495e;
    padding-bottom: 8px;
    margin-bottom: 18px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
    border-left: 4px solid #34495e;
    padding-left: 10px;
    background: rgba(236, 240, 241, 0.5);
    border-radius: 5px;
}

h3 {
    font-size: 1.75em;
    font-weight: 500;
    color: #5d6767;
    padding-bottom: 6px;
    margin-bottom: 16px;
    /*border-bottom: 1px dashed #5d6767;*/
    padding-left: 5px;
}

h4 {
    font-size: 1.5em;
    font-weight: 500;
    color: #95a5a6;
    margin-bottom: 14px;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
    background: linear-gradient(90deg, #ecf0f1, #f7f7f7);
    border-radius: 3px;
    padding: 8px;
}

h5 {
    font-size: 1.25em;
    font-weight: 400;
    color: #bdc3c7;
    margin-bottom: 12px;
    border-left: 3px solid #bdc3c7;
    padding-left: 10px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

h6 {
    font-size: 1em;
    font-weight: 400;
    color: #ecf0f1;
    margin-bottom: 10px;
    background-color: #34495e;
    padding: 6px 10px;
    border-radius: 4px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
}

th, td {
    border: 1px solid #ddd;
    padding: 8px;
}

th {
    background-color: #f2f2f2;
    font-weight: bold;
}

.err {
    color: indianred;
}